export const pageAnim = {
    hidden: {
        opacity: 0, 
        y: 300
    },
    shown: {
        opacity: 1,
        y: 0,
        transition: {
            duration: 1,
        }
    },
    exit: {
        opacity: 0, 
        y: 300,
        transition: {
            duration: 1,
        }
    }
}